/* Global Styles */
:root {
  --primary-color: #babdbf;
  --dark-color: #101011;
  --light-color: #ffffff;
}

.text-primary {
  color: #babdbf;
  color: var(--primary-color);
}

.text-dark {
  color: #101011;
  color: var(--dark-color);
}

.text-success {
  color: var(--success-color);
}

.text-danger {
  color: var(--danger-color);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-image: url(/bee-byte/Portfolio/static/media/image.2f178e1e.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #333;
}

a {
  color: #babdbf;
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  color: #666;
}

ul {
  list-style: none;
}

